"use client";
import { useState } from "react";

export default function EmbedDemoPage() {
  const [showIframe, setShowIframe] = useState(false);
  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gradient-to-br from-gray-50 to-blue-100 p-8">
      <h1 className="text-2xl font-bold mb-6 text-gray-800">网页内嵌套外部网页演示</h1>
      <button
        className="mb-8 px-6 py-3 rounded-xl bg-blue-500 text-white font-semibold shadow hover:bg-blue-600 transition"
        onClick={() => setShowIframe(true)}
      >
        点击嵌入百度首页
      </button>
      {showIframe && (
        <div className="w-full max-w-3xl aspect-video rounded-xl overflow-hidden shadow-lg border border-gray-200">
          <iframe
            src="https://ai.youdao.com/saas/qanything/#/home"
            title="问答"
            width="100%"
            height="100%"
            style={{ minHeight: 400, border: "none" }}
            allowFullScreen
          />
        </div>
      )}
    </div>
  );
} 